    key: 'amount',
    sorter: (a, b) => a.amount - b.amount,
    scopedSlots: { customRender: 'amount' },
  },
  {
    title: 'Purchased Date',
    dataIndex: 'purchase_date',
    key: 'purchase_date',
    sorter: (a, b) => a.purchase_date - b.purchase_date,
  },
  {
    title: 'Created Date',
    dataIndex: 'created_at',
    key: 'created_at',
    scopedSlots: { customRender: 'created_at' },
  },
]
const reportColumns = [
