    key: 'client_name',
    sorter: (a, b) => a.client_name - b.client_name,
    scopedSlots: { customRender: 'client_name' },
  },
  {
    title: 'Price in USD',
    dataIndex: 'price_usd',
    key: 'price_usd',
    sorter: (a, b) => a.price_usd - b.price_usd,
    scopedSlots: { customRender: 'price_usd' },
  },
  {
    title: 'Price in Naira',
    dataIndex: 'price_ngn',
    key: 'price_ngn',
    sorter: (a, b) => a.price_ngn - b.price_ngn,
    scopedSlots: { customRender: 'price_ngn' },
  },
  {
