  },
  {
    title: 'Paid Amount',
    dataIndex: 'paid_amount',
    key: 'paid_amount',
    scopedSlots: { customRender: 'paid_amount' },
  },
]

export default {
  data: function () {
    return {
      expenditureColumns,
      expenditureColumnsNoAdmin,
      reportColumns,
      debtorsColumns,
      incomeColumns,
      form: this.$form.createForm(this),
      currency: '',
      amount: '',
      formId: Number,
      reports: [],
      debtors: [],
      income: [],
      expenditures: [],
      user_type: this.$store.state.user.user.user_type,
    }
  },
  mounted() {
    this.getAllReports()
    this.getExpenditure()
  },
  methods: {
    updateReport(id) {
      const selectInput = document.getElementById('idselect_' + id).value
      let valueInput = document.getElementById('idvalue_' + id).value
      valueInput = parseInt(valueInput, 10)

      const requestPayload = {
        currency: selectInput,
        paid_amount: valueInput,
      }

      // here we are perfoming a data check, we need to get the price in USD and in Naira check if the
      // the prices corresponds with the value the user is giving based on the set currency,
      // if the price given is higher than the set price reject and throw an error

      // get the reports data
      var data = this.reports
      // use the id to get the index and value of the array
      var query = id
      // search through the array and get the index array with the passed value
      var index = data.map(function (img) { return img.id }).indexOf(query)

      // here we get the initial prices
      const basePriceUsd = parseInt(data[index].price_usd, 10)
      const basePriceNaira = parseInt(data[index].price_ngn, 10)

      // set proceed to indicate that the check is correct and we can proceed to send to db
      // eslint-disable-next-line no-unused-vars
      let proceed = false

      // now what we just need to check is if the given price is higher than the set price based on currency
      if (selectInput === '1') {
        // for usd
        console.log('selected usd',)
        if (valueInput > basePriceUsd) {
          this.$notification['warning']({
            message: 'Report Update',
            description: 'Opps! Price cannot be higher than price in USD',
          })
          proceed = false
        } else {
          proceed = true
        }
      } else {
        // for ngn
        console.log('selected naira',)
        if (valueInput > basePriceNaira) {
          this.$notification['warning']({
            message: 'Report Update',
            description: 'Opps! Price cannot be higher than price in Naira',
          })
          proceed = false
        } else {
          proceed = true
        }
      }

      if (proceed === true) {
        this.axios.post(`report/update/${id}`, requestPayload)
          .then(response => {
            // console.log(response)
            if (response.status === 200) {
              this.$notification['success']({
                message: 'Report Update',
                description: 'Report has been updated',
              })
              this.getAllReports()
            }
          })
          .catch(error => {
            // console.log(error)
            this.$notification['warning']({
              message: 'Report Update',
              description: 'Sorry an error occurred',
            })
            return error
          })
      }
    },
    getAllReports() {
      this.axios.get('report/all')
        .then(response => {
          // console.log(response.data.data)
          const dt = response.data.data.reports
          const dtt = response.data.data.debtor
          const dii = response.data.data.income
          const data = []
          const dataDebt = []
          const dataIncome = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              client_name: dt[i].client_full_name,
              price_usd: dt[i].total_usd,
              price_ngn: dt[i].total_naira,
              container_no: dt[i].container_no,
              status: dt[i].status,
              paid_amount: {
                amount: dt[i].paid_amount,
                stats: dt[i].status,
                id: dt[i].id,
              },
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Delete',
                url: dt[i].id,
              },
            }
            data.push(userData)
          }
          for (let d = 0; d < dtt.length; d++) {
            const userDataDebt = {
              client_name: dtt[d].shipment_shipping_mark,
              price_usd: dtt[d].shipment_total_usd,
              price_ngn: dtt[d].shipment_total_naira,
              container_no: dtt[d].shipment_container_no,
              balance: dtt[d].balance,
              paid_amount: dtt[d].report_paid_amount,
              id: dtt[d].id,
              key: dtt[d].id,
            }
            dataDebt.push(userDataDebt)
          }
          for (let k = 0; k < dii.length; k++) {
            const userDataIncome = {
              client_name: dii[k].shipment_shipping_mark,
              price_usd: dii[k].shipment_total_usd,
              price_ngn: dii[k].shipment_total_naira,
              container_no: dii[k].shipment_container_no,
              income: dii[k].income,
              paid_amount: dii[k].report_paid_amount,
              id: dii[k].id,
              key: dii[k].id,
            }
            dataIncome.push(userDataIncome)
          }

          this.reports = data
          this.debtors = dataDebt
          this.income = dataIncome
        })
        .catch(error => {
          // console.log(error)
          return error
        })
    },
    deleteExpenditure(id) {
      this.axios.post(`expenditure/delete/${id}`)
        .then(response => {
          // console.log(response)
          if (response.status === 200) {
            this.$notification['success']({
              message: 'Expenditures',
              description: 'You have successfully Deleted an Expenditure ',
            })

            const data = this.expenditures
            const query = id
            // search through the array and get the index array with the passed value
            const index = data.map(function (img) {
              return img.id
            }).indexOf(query)
            // filter through the data, remove the value we don't want (with index) and return the values we need
            const filteredItems = data.slice(0, index).concat(data.slice(index + 1, data.length))
            // replace the data with the returned value
            this.expenditures = filteredItems
          }
        })
        .catch(error => {
          // console.log(error)
          this.$notification['warning']({
            message: 'Expenditures',
            description: 'Sorry An error occurred',
          })
          return error
        })
    },
    getExpenditure() {
      this.axios.get('expenditure')
        .then(response => {
          // console.log('expenditure ', response.data.data)
          const dt = response.data.data.expenditures
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              amount: dt[i].amount,
              purchase_date: dt[i].purchase_date,
              particulars: dt[i].particulars,
              created_at: dt[i].created_at,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Delete',
                url: dt[i].id,
              },
            }
            data.push(userData)
          }
          this.expenditures = data
        })
        .catch(error => {
          // console.log(error)
          return error
        })
    },
  },
}
</script>
